import React from "react";

import { LayoutContainer, LayoutRow, LayoutItemWrapper, LayoutItem } from '../../layout/layout';
import { Heading, Text } from '../../base/typography';
import ButtonLink from '../../base/links/button-link';
import SvgWrapper from "../../base/svgwrapper";
import SkillsList from "./SkillsList";
import ExperienceList from "./ExperienceList";
import EducationList from "./EducationList";
import ScrollHint from '../../base/scroll-hint';
import { StaticImage } from "gatsby-plugin-image";

import SvgHighlightPlayful from '../../../images/svg/about-highlight-playful.svg';
import SvgHighlightCritical from '../../../images/svg/about-highlight-critical-thinker.svg';
import SvgHighlightLearning from '../../../images/svg/about-highlight-learning.svg';

import SvgSkills from '../../../images/svg/about-skills.svg';
import SvgExperience from '../../../images/svg/about-experience.svg';
import SvgEducation from '../../../images/svg/about-education.svg';


const AboutHero = () => {
    return (
        <LayoutContainer>
            <LayoutRow center reverse>
                    <LayoutItemWrapper height={'50vw'} center>
                        <LayoutItem maxWidth={'20rem'}>
                            <Heading level={1}>Hi, I'm Jan</Heading>
                            <Text bold>Creative Low Code developer from Rotterdam.</Text>
                            <ButtonLink href="https://www.linkedin.com/in/jan-wagenaar/" as="a">Connect with me at LinkedIn</ButtonLink>
                        </LayoutItem>
                    </LayoutItemWrapper>
                    <LayoutItemWrapper height={'50vw'} noPadding>
                        <StaticImage src="../../../images/pf.jpg" alt="Profile picture" style={{height: '100%'}} />
                    </LayoutItemWrapper>
                    <ScrollHint />
                </LayoutRow>
        </LayoutContainer>
    )
};

const AboutHighlights = () => {
    return (
    <LayoutContainer>
        <LayoutRow center>
                <LayoutItemWrapper center>
                    <LayoutItem maxWidth={'15rem'} center>
                        <SvgWrapper extrasmall>
                            <SvgHighlightPlayful />
                        </SvgWrapper>
                        <Heading level={3} center marginTop={'var(--spacing-s)'}>Playful at heart</Heading>
                        <Text center noMargin>Experimenting generates new insights.</Text>
                    </LayoutItem>
                </LayoutItemWrapper>
                <LayoutItemWrapper center>
                    <LayoutItem maxWidth={'15rem'} center>
                        <SvgWrapper extrasmall>
                            <SvgHighlightCritical />
                        </SvgWrapper>
                        <Heading level={3} center marginTop={'var(--spacing-s)'}>Critical thinker</Heading>
                        <Text center noMargin>Keep reinventing by analyzing yourselves.</Text>
                    </LayoutItem>
                </LayoutItemWrapper>
                <LayoutItemWrapper center>
                    <LayoutItem maxWidth={'15rem'} center>
                        <SvgWrapper extrasmall>
                            <SvgHighlightLearning />
                        </SvgWrapper>
                        <Heading level={3} center marginTop={'var(--spacing-s)'} >Learning by doing</Heading>
                        <Text center noMargin>Theory gets you halfway.</Text>
                    </LayoutItem>
                </LayoutItemWrapper>
            </LayoutRow>
    </LayoutContainer>
    )
};

const AboutContent = () => {
    return (
    <LayoutContainer>
        <LayoutRow center reverse>
            <LayoutItemWrapper center>
                <LayoutItem>
                    <Heading level={3} marginBottom={'2rem'}>Skills</Heading>
                    <SkillsList />
                </LayoutItem>
            </LayoutItemWrapper>
            <LayoutItemWrapper center>
                <SvgWrapper medium>
                    <SvgSkills />
                </SvgWrapper>
            </LayoutItemWrapper>
        </LayoutRow>
        <LayoutRow center>
            <LayoutItemWrapper center>
                <SvgWrapper medium>
                        <SvgExperience />
                </SvgWrapper>
            </LayoutItemWrapper>
            <LayoutItemWrapper center>
                <LayoutItem maxWidth={'30rem'} style={{ "width":"100%" }}>
                    <Heading level={3} marginBottom={'0'}>Places I've worked at</Heading>
                    <ExperienceList />
                </LayoutItem>
            </LayoutItemWrapper>
        </LayoutRow>
        <LayoutRow center reverse>
            <LayoutItemWrapper center>
                <LayoutItem maxWidth={'30rem'} style={{ "width":"100%" }}>
                    <Heading level={3} marginBottom={'0'}>What I've learned</Heading>
                    <EducationList />
                </LayoutItem>
            </LayoutItemWrapper>
            <LayoutItemWrapper center>
                <SvgWrapper medium>
                        <SvgEducation />
                </SvgWrapper>
            </LayoutItemWrapper>
        </LayoutRow>
    </LayoutContainer>
    )
};

export { AboutHero, AboutHighlights, AboutContent };