import styled, { css } from "styled-components";

const SvgWrapper = styled.div`
    position: relative; 
    height: auto;
    max-height: 100%;
    box-sizing: border-box;

    ${({extrasmall}) => extrasmall && css`
        width: clamp(50px, var(--img-xs), 100%);
    `}

    ${({small}) => small && css`
        width: clamp(75px, var(--img-s), 100%);
    `}

    ${({medium}) => medium && css`
        width: clamp(150px, var(--img-m), 100%);
    `}

    & svg {
        height: 100%;
        width: 100%;    
    }

    .dark-mode & svg .svg-fill-neutral-0 {
        fill: var(--color-neutral-0);
    }

    .dark-mode & svg .svg-fill-neutral-1 {
        fill: var(--color-neutral-1);
    }

    .dark-mode & svg .svg-fill-neutral-2 {
        fill: var(--color-neutral-2);
    }

    .dark-mode & svg .svg-fill-neutral-3 {
        fill: var(--color-neutral-3);
    }

    .dark-mode & svg .svg-fill-neutral-4 {
        fill: var(--color-neutral-4);
    }

    .dark-mode & svg .svg-fill-neutral-5 {
        fill: var(--color-neutral-5);
    }

    .dark-mode & svg .svg-fill-neutral-6 {
        fill: var(--color-neutral-6);
    }

    .dark-mode & svg .svg-fill-neutral-7 {
        fill: var(--color-neutral-7);
    }

    .dark-mode & svg .svg-fill-neutral-8 {
        fill: var(--color-neutral-8);
    }

    .dark-mode & svg .svg-fill-neutral-9 {
        fill: var(--color-neutral-9);
    }

    .dark-mode & svg .svg-fill-primary {
        fill: var(--color-primary-1);
    }

    .dark-mode & svg .svg-fill-neutral-white {
        fill: var(--color-text);
    }

    .dark-mode & svg .svg-stroke-neutral-0 {
        stroke: var(--color-neutral-0);
    }

    .dark-mode & svg .svg-stroke-neutral-1 {
        stroke: var(--color-neutral-1);
    }

    .dark-mode & svg .svg-stroke-neutral-2 {
        stroke: var(--color-neutral-2);
    }

    .dark-mode & svg .svg-stroke-neutral-3 {
        stroke: var(--color-neutral-3);
    }

    .dark-mode & svg .svg-stroke-neutral-4 {
        stroke: var(--color-neutral-4);
    }

    .dark-mode & svg .svg-stroke-neutral-5 {
        stroke: var(--color-neutral-5);
    }

    .dark-mode & svg .svg-stroke-neutral-6 {
        stroke: var(--color-neutral-6);
    }

    .dark-mode & svg .svg-stroke-neutral-7 {
        stroke: var(--color-neutral-7);
    }

    .dark-mode & svg .svg-stroke-neutral-8 {
        stroke: var(--color-neutral-8);
    }

    .dark-mode & svg .svg-stroke-neutral-9 {
        stroke: var(--color-neutral-9);
    }

    .dark-mode & svg .svg-stroke-neutral-white {
        stroke: var(--color-text);
    }

    .dark-mode & svg .svg-stroke-primary {
        stroke: var(--color-primary-1);
    }

`;

export default SvgWrapper;