import React from "react";
import styled from "styled-components";
import PropTypes from 'prop-types';

import { Text } from "../base/typography";
import { TextButton } from "../base/buttons";
import Tag from "../base/tag";
import MarkdownContent from "../markdown/markdown-wrapper";

const AccordionGroup = ({ items }) => {
    const [ accordionItems, setAccordionItems] = React.useState(() => items.map(item => { return {...item, isOpen: false} }));
    const [isShowClose, setIsShowClose] = React.useState(false);


    const triggerCloseAll = () => {
        const updatedItems = [...accordionItems];
        updatedItems.forEach(item => item.isOpen = false);
        setAccordionItems(updatedItems);
        setIsShowClose(false);
    }

    const triggerClose = (id) => {
        const updatedItems = [...accordionItems];
        const index = updatedItems.findIndex(item => item.id === id); 
        updatedItems[index].isOpen = !updatedItems[index].isOpen;
        setAccordionItems(updatedItems);

        const checkOpen = accordionItems.some(item => item.isOpen === true);
        setIsShowClose(checkOpen);
    }

    return (
    <AccordionGroupWrapper>  
        <AccordionGroupCollapseContainer>
            {isShowClose && <AccordionGroupCollapseButton onClick={triggerCloseAll}>Collapse all</AccordionGroupCollapseButton>}
        </AccordionGroupCollapseContainer>
        {accordionItems.map(item => <Accordion key={item.id} item={item} isOpen={item.isOpen} triggerClose={triggerClose} />)}
    </AccordionGroupWrapper>)
}

const AccordionGroupWrapper = styled.div`
    display: flex;
    flex-flow: column wrap;
    gap: var(--spacing-s);
`;

const Accordion = ({ item, isOpen, triggerClose }) => {
    const handleClose = (e) => {
        triggerClose(item.id);
        e.preventDefault();
    }



    return (
        <AccordionItem open={isOpen} >
            <AccordionItemSummary onClick={handleClose}>{ item.title } 
                <AccordionMarker isOpen={isOpen}><i className="fa-solid fa-chevron-down" /></AccordionMarker>
            </AccordionItemSummary>
            <AccordionItemContent>
                <Text>{ item.date }</Text>
                <Tag>{ item.tag }</Tag>
                <MarkdownContent 
                    htmlAst={item.description} 
                />
            </AccordionItemContent>
        </AccordionItem>
    )
};

const AccordionItem = styled.details`
    
`;


const AccordionItemSummary = styled.summary`
    display: inline-block;
    position: relative;
    font-weight: var(--font-weight-bold);
    padding: var(--spacing-s) 0;
    cursor: pointer;
    user-select: none;
    outline: none;
    list-style: none;

    &::-webkit-details-marker {
        display:none;
`;

const AccordionMarker = styled.div`
    display: inline-block;
    margin-left: var(--spacing-s);
    transition: transform var(--transition-fast);
    transform: rotate(${({isOpen}) => isOpen ? '180' : '0'}deg);
`;

AccordionMarker.propTypes = {
    isOpen: PropTypes.bool
}

const AccordionItemContent = styled.div`
    overflow: hidden;
    flex: 1;
    gap: var(--spacing-s);
`;

const AccordionGroupCollapseContainer = styled.div`
    height: 1.5rem;
`;

const AccordionGroupCollapseButton = styled(TextButton)`
    font-size: var(--font-size-s);
`;

export { Accordion, AccordionGroup };