import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { AccordionGroup } from "../../accordion/accordion";

const ExperienceList = () => {
    const data = useStaticQuery(graphql`
          query getExperience {
            allStrapiExperience(sort: {startedOn: DESC}) {
              nodes {
                jobTitle
                organizationName
                organizationURL
                isActive
                startedOn(formatString: "MMM YYYY")
                leftOn(formatString: "MMM YYYY")
                description {
                  data {
                    childMarkdownRemark {
                      htmlAst
                    }
                  }
                }
              }
            }
          }  
        `);

    const experiences= data.allStrapiExperience.nodes.map((item, index) => {
        return {
            id: index,
            title: item.organizationName,
            date: `${item.startedOn} - ${item.leftOn || 'current'}`,
            tag: item.jobTitle,
            description: item.description.data.childMarkdownRemark.htmlAst
        }
    });

    

    return <AccordionGroup items={experiences} />;
};

export default ExperienceList;