import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import { AccordionGroup } from "../../accordion/accordion";

const EducationList = () => {
    const data = useStaticQuery(graphql`
      query getEducation {
        allStrapiEducation(sort: {date: DESC}) {
          nodes {
            name
            date(formatString: "MMM YYYY")
            organizationName
            organizationURL
            description {
              data {
                childMarkdownRemark {
                  htmlAst
                }
              }
            }
          }
        }
      }   
    `);

    const educations= data.allStrapiEducation.nodes.map((item, index) => {
        return {
            id: index,
            title: item.name,
            date: item.date,
            tag: item.organizationName,
            description: item.description.data.childMarkdownRemark.htmlAst
        }
    });

    return <AccordionGroup items={educations} />;
};

export default EducationList;