import React from 'react';
import Page from '../components/layout/page';
import { AboutHero, AboutHighlights, AboutContent } from '../components/page-content/about/about-content';

const AboutPage = () => {
  return (
    <>
      <Page active="about">
        <AboutHero />
        <AboutHighlights />
        <AboutContent />
      </Page>
    </>
  )
}

export default AboutPage;