import * as React from 'react';
import styled from 'styled-components';
import SVGScrollArrow from '../../images/svg/scroll-arrow.svg';
import breakpoints from "../layout/breakpoints";

const ScrollHint = () => {
    const scrollDown = () => {window.scrollTo(0, 500)};


    return (
        <ScrollHintWrapper>
            <div style={{"transform":"rotate(90deg)"}}>
                <ScrollHintCaption onClick={scrollDown}>
                    Scroll
                </ScrollHintCaption>
                <ScrollHintArrow />
            </div>
        </ScrollHintWrapper>
    )
}

const ScrollHintWrapper = styled.div`
    display: none;
    position: absolute;
    right: 0.25rem;
    bottom: 0.75rem;
    transition: all ease-out .5s;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-name: bounce;
    animation-timing-function: linear;
    backface-visibility: hidden;
    z-index: 1;

    @keyframes bounce {
        0%   { transform: translateY(0); }
        50%  { transform: translateY(-0.625rem); }
        100% { transform: translateY(0); }
    }

    ${breakpoints.sm`
        display: block;
    `}
`;

const ScrollHintCaption = styled.p`
    display: inline-block;
    margin: 0;
    padding: 0;
    font-weight: var(--font-weight-bold);
`;

const ScrollHintArrow = styled(SVGScrollArrow)`
    display: inline-block;
    height: 0.7rem;
    width: 1.8rem;
    margin-left: var(--spacing-s);
    padding: 0; 
    fill: none;
    stroke: var(--color-neutral-9);
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: .07em;
    stroke-dasharray: 200;
    stroke-dashoffset: 0;
`;

export default ScrollHint;