import React from "react"
import { useStaticQuery, graphql } from "gatsby";
import SpacedList from "./ListSpaced";


const SkillsList = () => {
    const data = useStaticQuery(graphql`
      query getSkills {
        allStrapiSkill(sort: {order: ASC}) {
          nodes {
            level
            name
            order
          }
        }
      }
    `);

    const skills= data.allStrapiSkill.nodes.map((item, index) => {
        return {
            id: item.order,
            name: item.name,
            level: item.level
        }
    });

    return <SpacedList items={skills} />
};

export default SkillsList;