import React from 'react';
import styled from 'styled-components';
import { Text } from '../../base/typography';

const ListSpaced = ({items}) => {
    return (
        <ListSpacedWrapper>
            {items.map(item =>
                <ListSpacedItem key={item.id}>
                    <ListSpacedItemName>
                        {item.name}
                    </ListSpacedItemName>
                    <ListSpacedItemSpacer />
                    <ListSpacedItemLevel>
                        {item.level}
                    </ListSpacedItemLevel>
                </ListSpacedItem>
            )}
        </ListSpacedWrapper>
    )
};

const ListSpacedWrapper = styled.div`
    
`;

const ListSpacedItem = styled.div`
    display: flex;
    flex-flow: row nowrap;
    flex: 1 1 100%;
    margin-bottom: var(--spacing-s);
    align-items: flex-end;
`;

const ListSpacedItemName = styled(Text)`
    align-self: flex-start;
    flex-wrap: nowrap;
    margin: 0;
    font-weight: var(--font-weight-semibold);
`;

const ListSpacedItemSpacer = styled.div`
    flex: 1;
    height: 1rem;
    min-width: 1rem;
    margin: 0 var(--spacing-xs);
    border-bottom: var(--spacing-xxxs) dotted var(--color-neutral-9);
`;

const ListSpacedItemLevel = styled(Text)`
    align-self: flex-end;
    margin: 0;
    word-break: normal;
`;

export default ListSpaced;
